.property-description h5 {
  margin: 0px;
  font-size: 1.2em;
  font-weight: 500;
  color: rgb(24, 23, 23);
}

.property-description p {
  font-size: 12px;
}

.center {
  /* height:34vh; */
  height: fit-content;
  /* width:23%; */
  /* margin: 30px 30px; */
  margin: 30px 30px;
  /* margin-right: 20px; */
  display: flex;
  /* justify-content: center; */
  align-items: center;
  border-radius: 22px;
  /* box-shadow: rgb(46, 45, 45) 1px 2px 10px; */
}

/* End Non-Essential  */

.property-card {
  height: 18em;
  width: 14em;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  /* -webkit-box-orient:vertical;
  -webkit-box-direction:normal;
  -ms-flex-direction:column; */
  flex-direction: column;
  position: relative;
  /* -webkit-transition:all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
  -o-transition:all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition:all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1); */
  border-radius: 16px;
  overflow: hidden;
  /* -webkit-box-shadow:  15px 15px 27px #e1e1e3, -15px -15px 27px #ffffff; */
  /* box-shadow:  15px 15px 27px #e1e1e3, -15px -15px 27px #ffffff; */
  box-shadow: 0px 2px 7px rgb(80, 80, 80);
}
/* ^-- The margin bottom is necessary for the drop shadow otherwise it gets clipped in certain cases. */

/* Top Half of card, image. */

.property-image {
  height: 13em;
  width: 14em;
  /* padding:1em 2em; */
  position: Absolute;
  top: 0px;
  /* -webkit-transition:all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
  -o-transition:all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition:all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1); */
  /* background-image:url('https://cdn.photographylife.com/wp-content/uploads/2017/01/What-is-landscape-photography.jpg'); */
  /* background-size:cover;
  background-repeat:no-repeat; */
}

/* Bottom Card Section */

.property-description {
  background-color: #fafafc;
  height: 4em;
  width: 86%;
  position: absolute;
  bottom: 0em;
  /* -webkit-transition:all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
  -o-transition:all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1); */
  /* transition:all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1); */
  padding: 0.5em 1em;
  background: linear-gradient(
    to right,
    rgb(243, 138, 130) 0%,
    rgb(243, 169, 169) 50%,
    rgb(250, 197, 161) 100%
  );
}

/* Social Icons */

.property-social-icons {
  /* width:1em; */
  /* height:20px; */
  /* background-color:black; */
  display: flex;
  flex-direction: row;
  position: absolute;
  bottom: 0.2em;
  left: 0.8em;
  -webkit-transition: all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
  -o-transition: all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.read-button {
  border-radius: 10px;
  height: 20px;
  width: 60px;
  background-color: white;
  text-align: center;
  padding-bottom: 3px;
  margin-right: 5px;
  color: rgb(32, 32, 32);
  font-weight: 600;
}

.downloadbutton {
  color: rgb(44, 44, 44);
}

